<template>
  <div class="community">
    <CommunityTutorial></CommunityTutorial>
    <header class="header" v-if="store.btnToggle">
      <h1 class="h1">IDN Community</h1>
    </header>
    <header class="header">
      <h2>Popular Novels For Reading</h2>
      <button class="button" @click="importFile()">Submit Image/Text</button>
      <input type="file" id="file-input" style="display: none" @change="onFileInputChange">
    </header>
    <br />
    <div class="novels-container">
      <div class="novel-item" v-for="(novel, index) in this.novelStore.novels" :key="novel.title">
        <div>
          <div @click="readNovel(index)">
            <img :src="novel.cover" :alt="novel.title" class="novel-cover" />
          </div>
        </div>
        <h3>{{ novel.title }}</h3>
      </div>
    </div>

  </div>
</template>

<script>
import { useNovelStore } from "../stores/novelStore";
import { useStore } from "../stores/store";
import { useUserStore } from '../stores/userStore';
import { app } from "../../firebaseConfig";
import { getStorage, ref, uploadBytesResumable, getDownloadURL, listAll } from "firebase/storage";
import { getAuth } from "firebase/auth";
import CommunityTutorial from '../components/tutorials/CommunityTutorial.vue';

export default {
  components: {
    CommunityTutorial,
  },
  setup() {
    const novelStore = useNovelStore();
    const store = useStore();
    const user = useUserStore();
    return { user, store, novelStore };
  },
  async mounted() {
    const auth = getAuth();
    this.novelStore.novels = [];
    if (auth.currentUser != null) {
      await this.fetchNovelsFromFirebase();
    } else {
      alert("Please go to the Login interface to log in/register first")
    }
  },
  methods: {
    async fetchNovelsFromFirebase() {
      const storage = getStorage(app);
      const novelsRef = ref(storage, 'Novels');
      const coverRef = ref(storage, 'Novels-Cover');
      // List all files in the 'Novels' folder
      const resN = await listAll(novelsRef);
      const files = resN.items;
      // Get the download URL for each file and store the novel information in the novelStore
      files.forEach(async (file) => {
        const downloadURL = await getDownloadURL(file);
        const title = file.name.replace('.txt', ''); // Extract the title from the file name
        this.novelStore.novels.push({
          title: title,
          cover: null,
          content: downloadURL,
        });
      });
      const resNC = await listAll(coverRef);
      const filesC = resNC.items;
      filesC.forEach(async (fileC) => {
        const downloadURLC = await getDownloadURL(fileC);
        const title = fileC.name.replace('.png', ''); // Extract the title from the file name
        for (let i = 0; i < this.novelStore.novels.length; i++) {
          if (this.novelStore.novels[i].title === title) {
            this.novelStore.novels[i].cover = downloadURLC;
          }
        }
      });
      const deCoverRef = ref(storage, 'Novels-Cover/Default.png');
      const downloadDeCoverURL = await getDownloadURL(deCoverRef);
      this.novelStore.novels.forEach((novel) => { novel.cover = novel.cover === null ? downloadDeCoverURL : novel.cover; })
    },

    async uploadTextFileToFirebase(file) {
      const storage = getStorage(app);
      const storageRef = ref(storage, `Novels/${file.name}`);
      const token = this.user.userInfo.token;
      const uploadTask = uploadBytesResumable(storageRef, file, {
        // 包含身份验证令牌的自定义元数据
        customMetadata: {
          'firebaseAuth': token,
        }
      });
      uploadTask.on('state_changed',
        (snapshot) => {
          // Observe state change events such as progress, pause, and resume
          // Get task progress, including the number of bytes uploaded and the total number of bytes to be uploaded
          const progress = (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
          console.log('Upload is ' + progress + '% done');
          switch (snapshot.state) {
            case 'paused':
              console.log('Upload is paused');
              break;
            case 'running':
              console.log('Upload is running');
              break;
            default:
              break;
          }
        },
        (error) => {
          // Handle unsuccessful uploads
          console.error('Upload failed:', error);
          alert("Uploading to the server failed, please check if you are logged in")
        },
        async () => {
          // Handle successful uploads on complete
          const downloadURL = await getDownloadURL(storageRef);
          alert('File available at', downloadURL);
          // Call a function to handle the file URL, e.g., save it in your database
          // handleFileURL(downloadURL);
        }
      );
    },


    async readNovel(index) {
      const novelURL = this.novelStore.novels[index].content;
      const response = await fetch(novelURL, {
        mode: 'cors',
      });

      if (response.ok) {
        const text = await response.text();
        this.novelStore.setCurrentNovel(text);
        // 使用 this.$router.push 进行跳转
        await this.$router.push({
          name: "reader",
          query: {
            startReading: true,
          },
        });
      } else {
        console.error("Failed to download the novel from Firebase Storage");
      }
    },

    importFile() {
      const fileInput = document.getElementById("file-input");
      fileInput.setAttribute("multiple", "multiple"); // 允许选择多个文件
      fileInput.click();
    },
    onFileInputChange(event) {
      const files = event.target.files;
      for (const file of files) {
        if (file.type.startsWith("image/")) {
          this.uploadImageFileToFirebase(file); // 上传图片文件到 Firebase Storage
        } else if (file.type === "text/plain") {
          this.uploadTextFileToFirebase(file); // 上传文本文件到 Firebase Storage
          const reader = new FileReader();
          reader.onload = (event) => {
            const text = event.target.result;
            this.parseNovelText(text);
          };
          reader.readAsText(file);
        } else {
          alert("Please import a supported text or image file");
        }
      }
    },

    // 添加一个新方法用于上传图片文件
    async uploadImageFileToFirebase(file) {
      const storage = getStorage(app);
      const storageRef = ref(storage, `Novels-Cover/${file.name}`);
      const token = this.user.userInfo.token;
      const uploadTask = uploadBytesResumable(storageRef, file, {
        customMetadata: {
          firebaseAuth: token,
        },
      });

      uploadTask.on('state_changed',
        (snapshot) => {
          const progress = (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
          console.log('Upload is ' + progress + '% done');
          switch (snapshot.state) {
            case 'paused':
              console.log('Upload is paused');
              break;
            case 'running':
              console.log('Upload is running');
              break;
            default:
              break;
          }
        },
        (error) => {
          console.error('Upload failed:', error);
          alert("Uploading to the server failed, please check if you are logged in");
        },
        async () => {
          const downloadURL = await getDownloadURL(storageRef);
          alert('File available at', downloadURL);
        }
      );
    },

    parseNovelText(text) {
      const lines = text.trim().split('\n');

      //let author = '';
      //let content = '';
      //let l = 0;
      this.store.btnToggle = !this.store.btnToggle;
      if (lines[0].startsWith('Story Title: ')) {
        let title = lines[0].slice(13);
        this.novelStore.novels.push({
          "title": title,
        })
      } else {
        alert("Please import a text file in the format used by this site")
      }


    }
  },
};
</script>

<style>
.community {
  text-align: left;
}

.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px;
}

.novels-container {
  margin-left: 5em;
  display: flex;
  flex-wrap: wrap;
  justify-content: left;
  gap: 16px;
}

.novel-item>div {
  display: inline-block;
  text-align: center;
}

.novel-cover {
  width: 200px;
  height: auto;
  cursor: pointer
}
</style>
